let pos2;
let shrink = false
let cieling = availWidth / 2
let start1 = -availWidth
let start2 = -availWidth
let card1;
let card2;
let xOff = 0
let pPosition = -800
let pArray = []
let dArray = []
let sArray = []
let count = 0



function initPlayer() {
  for (i=0; i<=4; i++) {
    let x = new Card
    pArray.push(x)
    sArray.push(-800)
  }
}

function initDealer() {
  for (i=0; i<=4; i++) {
    let x = new Card
    dArray.push(x)
  }
}

function renderCards(data,who) {
  let y;
  switch(who) {
    case 'player':
      y = 200
      break
    case 'dealer':
      y = -200
      break
  }

  for (let x of data) {
    push()
    translate(sArray[count],y)
    x.create()
    if (sArray[count] <= pPosition) {
      sArray[count] += 10
    }
    pPosition += 200
    pop()
    count += 1
  }
  count = 0
  pPosition = -800  
}

function preload() {
  // preload() runs once
  img = loadImage('../static/sa.jpg');
}


function setup() {
  createCanvas(availWidth, availHeight, WEBGL)
  //angleMode(DEGREES)
  initPlayer()
  initDealer()
}
  
